import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';

import{environment}from'../environments/environment';
import { Employee } from './model/employee';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  // baseUrl:string="http://localhost:8080/";
  baseUrl:string=environment.baseUrl;
  constructor(private http:HttpClient) { }

  logincheck(obj){
  return (this.http.post(`${this.baseUrl}login`,obj))
  }

  GetAllEmployeeData(){
  return (this.http.get(`${this.baseUrl}employee/getAllEmployee`));
  // return (this.http.get <Employee[]>(`${this.baseUrl}
  // employee/getAllEmployee`));
  }

  GetEmployeeById(id){
   let params=new HttpParams();
   params=params.append('id',id);
  //const opts={id:"12"};
  //const params={id:12};
  return (this.http.get(`${this.baseUrl}employee/getParticularEmployee`,
  {params:params}));
  }
 getAllCountry (){
    return (this.http.get(`${this.baseUrl}country/getAllCountry`))
   }

   addEmployee(obj){
    return (this.http.post(`${this.baseUrl}employee/addEmployee`,obj,{
           responseType:'text'
    }));
  }
 updateEmployee(obj){
 return(this.http.put(`${this.baseUrl}employee/updateEmployee`,obj,
 {responseType:'text'}))
 }
 DeleteRecord(id){
  let params=new HttpParams();
  params=params.append('id',id);
  return(this.http.delete(`${this.baseUrl}employee/deleteEmployee`,
  {params:params,responseType:'text' }));
}
}
